import { Typography } from "antd";
import React from "react";
import { BASE, DISPLAY_SIZE_DIVIDE, X_LARGE } from "src/utils/space";
import { TITLE_STYLE } from "src/utils/style";
import styled from "styled-components";
import Responsive from "src/components/atoms/Responsive";

const { Title } = Typography;
const ValueWrapper = styled.div`
  padding: ${BASE} 0px;
`;

const WordWrapper = styled.div`
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: ${X_LARGE};
`;
const ValueWord = styled(Title)`
  margin: 0px !important;
  ${TITLE_STYLE}
`;

type Props = {
  valueWord1: string;
  valueWord2: string;
  valueWord3: string;
  valuePara1: React.ReactNode;
  valuePara2: React.ReactNode;
  valuePara3: React.ReactNode;
};
const Value: React.FC<Props> = ({
  valueWord1,
  valueWord2,
  valueWord3,
  valuePara1,
  valuePara2,
  valuePara3,
}) => (
  <>
    <Responsive min={DISPLAY_SIZE_DIVIDE}>
      <WordWrapper>
        <ValueWord>{valueWord1}</ValueWord>
        <ValueWord>{valueWord2}</ValueWord>
        <ValueWord>{valueWord3}</ValueWord>
      </WordWrapper>
      <ValueWrapper>{valuePara1}</ValueWrapper>
      <ValueWrapper>{valuePara2}</ValueWrapper>
      <ValueWrapper>{valuePara3}</ValueWrapper>
    </Responsive>
    <Responsive max={DISPLAY_SIZE_DIVIDE}>
      <ValueWrapper>
        <ValueWord>{valueWord1}</ValueWord>
        {valuePara1}
      </ValueWrapper>
      <ValueWrapper>
        <ValueWord>{valueWord2}</ValueWord>
        {valuePara2}
      </ValueWrapper>
      <ValueWrapper>
        <ValueWord>{valueWord3}</ValueWord>
        {valuePara3}
      </ValueWrapper>
    </Responsive>
  </>
);

export default Value;
