import { Typography } from "antd";
import React from "react";
import { BASE, DISPLAY_SIZE_DIVIDE } from "src/utils/space";
import Responsive from "src/components/atoms/Responsive";
import styled from "styled-components";
import "./style.css";

const { Title, Text } = Typography;

type Props = {
  contents: [string | React.ReactNode, string | React.ReactNode][];
};

const Table = styled.table`
  min-width: 350px;
  border: 0px;
  table-layout: fixed;
`;

const COMMON_STYLE = `
  padding-bottom: ${BASE};
  line-height: 2.0;
  border: 0px;
  table-layout: fixed;
  vertical-align: top;

  font-weight: normal;
  text-align: left !important;
`;
const ColTd = styled.td`
  padding-right: 16px;
  border-right: solid 1px #ddd !important;
  word-break: keep-all;
  ${COMMON_STYLE}
`;
const ColTh = styled.th`
  padding-right: 16px;
  border-right: solid 1px #ddd !important;
  word-break: keep-all;
  ${COMMON_STYLE}
`;
const DescTd = styled.td`
  padding-left: 16px;
  ${COMMON_STYLE}
`;
const DescTh = styled.th`
  padding-left: 16px;
  ${COMMON_STYLE}
`;

const DescriptionTable: React.FC<Props> = ({ contents }) => (
  <>
    <Responsive min={DISPLAY_SIZE_DIVIDE}>
      <Table>
        <tbody>
          {contents.map(([col, desc], i) =>
            i > 0 ? (
              <tr key={i}>
                <ColTd>{col}</ColTd>
                <DescTd>{desc}</DescTd>
              </tr>
            ) : (
              <tr key={i}>
                <ColTh>{col}</ColTh>
                <DescTh>{desc}</DescTh>
              </tr>
            )
          )}
        </tbody>
      </Table>
    </Responsive>
    <Responsive max={DISPLAY_SIZE_DIVIDE}>
      <Wrapper>
        {contents.map(([col, desc], i) => (
          <Typography key={i}>
            <Title>{col}</Title>
            <Text>{desc}</Text>
          </Typography>
        ))}
      </Wrapper>
    </Responsive>
  </>
);

const Wrapper = styled.div`
  display: inline;
`;

export default DescriptionTable;
