import { Button, Drawer, Menu, Typography } from "antd";
import React, { useCallback } from "react";
import { useHistory } from "react-router-dom";
import { CONTACT_URL } from "src/utils/misc";
import { HEADER_HEIGHT } from "src/utils/space";
import styled from "styled-components";
import useWindowScrollPosition from "@rehooks/window-scroll-position";
import { useSpring, animated } from "react-spring";

type PushCloseButtonProps = {
  history: ReturnType<typeof useHistory>;
  onClose: () => void;
  to: string;
};

type Props = {
  onClose: () => void;
  visible: boolean;
};

const StyledButton = styled(Button)`
  width: 100%;
  height: 100%;
`;
const Bg = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: ${HEADER_HEIGHT};
  background-color: white;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.2);
`;

const PushCloseButton: React.FC<PushCloseButtonProps> = ({ children, history, onClose, to }) => {
  const handleClose = useCallback(() => {
    history.push(to);
    onClose();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <StyledButton type="link" onClick={handleClose}>
      {children}
    </StyledButton>
  );
};

const CustomDrawer = (props: Props) => {
  const history = useHistory();
  const position = useWindowScrollPosition();
  const animateProps = useSpring({
    opacity: position.y > 200 ? 1 : 0,
  });
  return (
    <>
      <animated.div style={{ ...animateProps, zIndex: 3 }}>
        <Bg />
      </animated.div>
      <Drawer placement="right" closable={false} onClose={props.onClose} visible={props.visible}>
        <Menu style={{ borderRight: "0px" }}>
          <Menu.Item>
            <PushCloseButton history={history} onClose={props.onClose} to="/">
              <Typography>Home</Typography>
            </PushCloseButton>
          </Menu.Item>
          <Menu.Item>
            <PushCloseButton history={history} onClose={props.onClose} to="/service">
              <Typography>Service</Typography>
            </PushCloseButton>
          </Menu.Item>
          <Menu.Item>
            <PushCloseButton history={history} onClose={props.onClose} to="/company">
              <Typography>Company</Typography>
            </PushCloseButton>
          </Menu.Item>
          <Menu.Item>
            <PushCloseButton history={history} onClose={props.onClose} to="/news">
              <Typography>News</Typography>
            </PushCloseButton>
          </Menu.Item>
          <Menu.Item>
            <a href={CONTACT_URL}>
              <StyledButton type="link">
                <Typography>Contact</Typography>
              </StyledButton>
            </a>
          </Menu.Item>
        </Menu>
      </Drawer>
    </>
  );
};

export default CustomDrawer;
