import { Layout } from "antd";
import React, { useCallback, useEffect, useState } from "react";
import { HashRouter as Router, Link, Route, Switch, useLocation } from "react-router-dom";
import ButtonsMenu from "src/components/atoms/ButtonsMenu";
import HamburgerMenu from "src/components/atoms/HamburgerMenu";
import Drawer from "src/components/molecules/Drawer";
import GecsText from "src/imgs/GecsText.png";
import pages from "src/Pages";
import { SITE_BACKGROUND_COLOR } from "src/utils/color";
import {
  BASE,
  FOOTER_HEIGHT,
  HEADER_HEIGHT,
  LARGE,
  PAGE_SIDE_PADDING,
  X_LARGE,
} from "src/utils/space";
import styled from "styled-components";

const { Header, Content, Footer } = Layout;

const StyledHeader = styled(Header)`
  width: 100%;
  height: ${HEADER_HEIGHT};
  position: fixed;
  z-index: 1;

  padding-right: 0;
`;
const StyledFooter = styled(Footer)`
  padding-top: ${X_LARGE};
  padding-bottom: ${BASE};
  text-align: center;
  background-color: ${SITE_BACKGROUND_COLOR};
`;

const ContentWrapper = styled(Content)`
  min-height: calc(100vh - ${HEADER_HEIGHT} - ${FOOTER_HEIGHT} - ${X_LARGE} - ${BASE});
  margin-top: ${HEADER_HEIGHT};
  padding: 0px ${PAGE_SIDE_PADDING};
`;

const StyledLayout = styled(Layout)`
  padding: 0px !important;
  background-color: ${SITE_BACKGROUND_COLOR};
`;

const PagePadding = styled.div`
  padding: ${X_LARGE} 0px;
`;
const StyledGecsText = styled.img`
  padding: 4px ${LARGE};
  height: ${HEADER_HEIGHT};
`;

function Routes() {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
    // @ts-ignore
    window.gtagPageview(pathname);
  }, [pathname]);
  return (
    <Switch>
      {pages.map(({ path, noPadding, View }, i) => (
        <Route key={i} exact path={path}>
          {noPadding ? (
            <View />
          ) : (
            <PagePadding>
              <View />
            </PagePadding>
          )}
        </Route>
      ))}
    </Switch>
  );
}

function App() {
  const [drawerVidible, setDrawerVisible] = useState(false);
  const handleOpenDrawer = useCallback(() => setDrawerVisible(true), []);
  const handleCloseDrawer = useCallback(() => setDrawerVisible(false), []);
  return (
    <Router>
      <StyledLayout>
        <StyledHeader>
          <Link to="/">
            <StyledGecsText src={GecsText} />
          </Link>

          <HamburgerMenu onClick={handleOpenDrawer} />

          <ButtonsMenu />
        </StyledHeader>
        <Drawer onClose={handleCloseDrawer} visible={drawerVidible} />

        <ContentWrapper className="site-layout">
          <Routes />
        </ContentWrapper>

        <StyledFooter>
          <p>Copyright ©︎ {new Date().getFullYear()} GeCS, Inc. </p>
          <p>All rights reserved.</p>
        </StyledFooter>
      </StyledLayout>
    </Router>
  );
}

export default App;
