import { useEffect, useState } from "react";
import articleConfig from "src/articles/article-config";

export type Article = {
  id: string;
  date: string;
  title: string | React.ReactNode;
  body: string;
};
export type ArticleThumbnail = {
  id: string;
  date: string;
  title: string | React.ReactNode;
};

export const useArticle = (id: string): [Article | null, Boolean] => {
  const [article, setArticle] = useState<Article | null>(null);
  const [loading, setLoading] = useState<Boolean>(true);
  useEffect(() => {
    setLoading(true);
    const markdown = articleConfig[id];
    if (!markdown) {
      setLoading(false);
      setArticle(null);
      return;
    }
    fetch(markdown.path)
      .then((response) => response.text())
      .then((text) =>
        setArticle({
          id,
          date: markdown.date,
          title: markdown.title,
          body: text,
        })
      )
      .finally(() => setLoading(false));
  }, [id]);
  return [article, loading];
};

export const useArticles = (): ArticleThumbnail[] => {
  const articleEntries = Object.entries(articleConfig).sort((a, b) =>
    a[1].date === b[1].date ? 0 : a[1].date < b[1].date ? 1 : -1
  );
  return articleEntries.map(([id, { date, title }]) => ({
    id,
    date,
    title,
  }));
};
