import { LeftOutlined } from "@ant-design/icons";
import { Button, Card, Result, Skeleton, Typography } from "antd";
import React, { useCallback } from "react";
import ReactMarkdown from "react-markdown/with-html";
import { useHistory, useParams } from "react-router-dom";
import ContentWrapper from "src/components/atoms/ContentWrapper";
import { useArticle } from "src/hooks/useArticle";
import { LARGE } from "src/utils/space";
import styled from "styled-components";

const { Title, Text } = Typography;

const ArticleTitle = styled(Title)`
  white-space: normal;
`;
const StyledCard = styled(Card)`
  padding: ${LARGE} 0px;
`;
const ContactButton = styled(Button)`
  width: 100%;
  max-width: 300px;
`;

const BackButton = styled(Button)`
  margin: ${LARGE} 0px;
`;

const Markdown = styled(ReactMarkdown)``;

const NewsNotFound: React.FC = () => {
  const history = useHistory();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const backToTop = useCallback(() => history.push("/"), []);
  return (
    <StyledCard>
      <Result
        status="404"
        title="404"
        subTitle="お探しのページは見つかりませんでした。"
        extra={
          <ContactButton type="primary" shape="round" size="large" onClick={backToTop}>
            トップページに戻る
          </ContactButton>
        }
      />
    </StyledCard>
  );
};

const News: React.FC = () => {
  const history = useHistory();
  const handleBack = useCallback(() => {
    history.goBack();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const { id } = useParams<{ id: string }>();
  const [article, loading] = useArticle(id);
  return (
    <ContentWrapper>
      <BackButton size="large" icon={<LeftOutlined />} onClick={handleBack}>
        戻る
      </BackButton>
      {article ? (
        <StyledCard
          title={
            <>
              <Text>{article.date}</Text>
              <ArticleTitle level={2}>{article.title}</ArticleTitle>
            </>
          }
        >
          <Markdown
            source={article.body}
            className="markdown-article"
            escapeHtml={false}
            // renderers={{ img: (props) => <img {...props} /> }}
          />
        </StyledCard>
      ) : loading ? (
        <StyledCard title={<Skeleton active paragraph={{ rows: 0 }} />}>
          <Skeleton active paragraph={{ rows: 6 }} />
        </StyledCard>
      ) : (
        <NewsNotFound />
      )}
    </ContentWrapper>
  );
};

export default News;
