import { Typography } from "antd";
import React from "react";
import ContentWrapper from "src/components/atoms/ContentWrapper";
import NewsCard, { SkeletonCard } from "src/components/molecules/NewsCard";
import { useArticles } from "src/hooks/useArticle";
import styled from "styled-components";

const { Title } = Typography;

const PageTitle = styled(Title)`
  font-size: 48px !important;
`;

const Center = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
`;

const News: React.FC = () => {
  const articles = useArticles();
  return (
    <ContentWrapper>
      <PageTitle>News</PageTitle>
      <Center>
        {articles ? (
          articles.map(({ id, date, title }, i) => (
            <NewsCard id={id} date={date} title={title} key={i} />
          ))
        ) : (
          <>
            <SkeletonCard />
            <SkeletonCard />
            <SkeletonCard />
          </>
        )}
      </Center>
    </ContentWrapper>
  );
};

export default News;
