import { Button, Menu } from "antd";
import React, { useCallback } from "react";
import { useHistory } from "react-router-dom";
import { CONTACT_URL } from "src/utils/misc";
import { HEADER_HEIGHT } from "src/utils/space";
import styled from "styled-components";

const StyledMenu = styled(Menu)`
  display: inline;
  @media screen and (max-width: 720px) {
    display: none;
  }
`;

type PushCloseButtonProps = {
  history: ReturnType<typeof useHistory>;
  to: string;
};

const StyledButton = styled(Button)`
  color: black;
  height: ${HEADER_HEIGHT};
`;

const MenuButton: React.FC<PushCloseButtonProps> = ({ children, history, to }) => {
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const onClick = useCallback(() => history.push(to), []);
  return (
    <StyledButton type="link" onClick={onClick}>
      {children}
    </StyledButton>
  );
};

const ButtonsMenu: React.FC = () => {
  const history = useHistory();
  return (
    <StyledMenu
      theme="dark"
      mode="horizontal"
      defaultSelectedKeys={["2"]}
      style={{ lineHeight: "0px", float: "right" }}
    >
      <Menu.Item key="1" disabled={true} style={{ backgroundColor: "transparent" }}>
        <MenuButton to="/" history={history}>
          Home
        </MenuButton>
        <MenuButton to="/service" history={history}>
          Service
        </MenuButton>
        <MenuButton to="/company" history={history}>
          Company
        </MenuButton>
        <MenuButton to="/news" history={history}>
          News
        </MenuButton>

        <StyledButton type="link">
          <a href={CONTACT_URL}>Contact</a>
        </StyledButton>
      </Menu.Item>
    </StyledMenu>
  );
};

export default ButtonsMenu;
