import React from "react";
import styled from "styled-components";

type Props = {
  min?: string;
  max?: string;
};

const MinMaxBr = styled.br<Props>`
  display: none;
  @media screen and (max-width: ${(props) => props.max}) {
    display: inline;
  }
  @media screen and (max-width: ${(props) => props.min}) {
    display: none;
  }
`;
const MinBr = styled.br<Props>`
  display: inline;
  @media screen and (max-width: ${(props) => props.min}) {
    display: none;
  }
`;
const MaxBr = styled.br<Props>`
  display: none;
  @media screen and (max-width: ${(props) => props.max}) {
    display: inline;
  }
`;

const LimitedBr: React.FC<Props> = ({ min, max }) => {
  if (min && max) return <MinMaxBr min={min} max={max} />;
  if (min) return <MinBr min={min} />;
  if (max) return <MaxBr max={max} />;
  return <br />;
};

export default LimitedBr;
